@import '../../assets/css/variable.less';
@border: 1px solid @divider_color;

* {
  box-sizing: border-box;
}

.deri-num-slider {
  padding: 0 24px;

  .deri-cliam-remaining {
    margin-top: 16px;
    font-size: 18px;
  }
}

.deri-claim-slider {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  margin: 8px 0;

  .time-slider {
    width: 675px;
  }
}

.pool-detail {
  font-weight: 500;

  .font-color {
    color: @whiteFontColor;
    font-weight: bold;
  }

  .back-pool {
    height: 108px;
    border-top: @border;
    display: flex;
    font-size: 24px;

    a {
      text-decoration: none;
    }


    .icon-back {
      height: 48px;
      display: flex;
      align-items: center;
      cursor: pointer;

      span {
        color: #FFFFFF;
      }
    }

    img {
      width: 24px;
      height: 24px;
      margin-right: 16px;
    }
  }

  .pool-deatail-info {
    .stake {
      width: 80px;
      height: 20px;
      margin-left: 16px;
      display: flex;
      align-items: center;
      font-size: 12px;
      justify-content: space-between;
      padding: 12px;
      border: 1px solid rgba(224, 236, 255, 0.5);
      border-radius: 100px;
    }

    .btoken-logo {
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 20px;
        height: 20px;
      }
    }

    .WBNB,
    .BNB,
    .WMATIC,
    .amUSDC,
    .BUSD,
    .USDT,
    .CAKE,
    .DERI,
    .TBUSD,
    .SXP,
    .WBNB,
    .WBTC,
    .WETH,
    .ETH,
    .LTC,
    .XRP,
    .BTCB,
    .ADA,
    .CAKE,
    .MATIC,
    .USDC,
    .AAVE,
    .TUSD,
    .DAI,
    .DOT,
    .LINK,
    .XVS,
    .TRX,
    .TDERI,
    .UST,
    .DOGE,
    .LUNA {
      background-size: 20px 20px;
      background-repeat: no-repeat;
      background-position: center;
    }

    .LTC {
      background-image: url('../../assets/img/LTC.svg');
    }

    .DOGE {
      background-image: url('../../assets/img/DOGE.svg');
    }

    .UST {
      background-image: url('../../assets/img/UST.svg');
    }

    .LUNA {
      background-image: url('../../assets/img/LUNA.svg');
    }

    .XRP {
      background-image: url('../../assets/img/XRP.svg');
    }

    .XVS {
      background-image: url('../../assets/img/XVS.svg');
    }

    .DAI {
      background-image: url('../../assets/img/DAI.svg');
    }

    .DOT {
      background-image: url('../../assets/img/DOT.svg');
    }

    .LINK {
      background-image: url('../../assets/img/LINK.svg');
    }

    .ADA {
      background-image: url('../../assets/img/ADA.svg');
    }

    .AAVE {
      background-image: url('../../assets/img/AAVE.svg');
    }

    .TUSD {
      background-image: url('../../assets/img/TUSD.svg');
    }

    .TRX {
      background-image: url('../../assets/img/TRX.svg');
    }

    .SXP {
      background-image: url('../../assets/img/SXP.svg');
    }

    .USDC {
      background-image: url('../../assets/img/USDC.svg');
    }

    .DERI,
    .TDERI {
      background-image: url('../../assets/img/logo.svg');
    }

    .ETH,
    .WETH {
      background-image: url('../../assets/img/ETHUSD.svg');
    }

    .BTCB {
      background-image: url('../../assets/img/BTCUSD.svg');
    }

    .WBTC {
      background-image: url('../../assets/img/WBTC.svg');
    }

    .WBNB,
    .BNB {
      background-image: url('../../assets/img/BNB.svg');
    }

    .WMATIC,
    .MATIC {
      background-image: url('../../assets/img/MATIC.svg');
    }

    .amUSDC {
      background-image: url('../../assets/img/amusdc.svg');
    }

    .BUSD,
    .TBUSD {
      background-image: url('../../assets/img/BUSD.svg');
    }

    .USDT {
      background-image: url('../../assets/img/USDT.svg');
    }

    .CAKE {
      background-image: url('./img/Pancake.svg');
    }

    .pool-detail-basetoken-info {
      .search-box {
        margin-top: 10px;
        display: flex;
        justify-content: center;

      }

      .select-basetoken-info {
        .select-coin-title {
          padding-left: 24px;
          color: @whiteFontColor;
          font-size: 18px;
        }

        .coin-search {
          position: relative;
        }

        .coin-list {
          margin-top: 16px;
          position: relative;

          .coin-box {
            height: 100px;
            cursor: pointer;
            border-radius: 4px;
            padding: 0 24px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border: 1px solid transparent;

            .base-apy-info {
              display: flex;
              align-items: center;

              .base-apy {
                // display: flex;
                font-size: 14px;
              }

              .apy-base-staked {
                .base-name {
                  color: @whiteFontColor;
                  font-size: 18px;
                  font-weight: 600;
                  white-space: nowrap;
                }

                .base-apy {
                  font-weight: 500;
                }
              }
            }

            .btoken-logo {
              width: 40px;
              height: 40px;
              background-color: rgba(255, 255, 255, 0.1);
              border-radius: 50%;
              margin-right: 16px;
              display: flex;
              align-items: center;
            }

            .lp-btoken-logo {
              display: flex;
              align-items: center;
              margin-right: 16px;

              .btoken-logo {
                margin-right: 0;
              }

              .conect-logo {
                font-size: 20px;
                margin: 0 5px;
              }
            }


          }

          .coin-box:hover {
            border: 1px solid rgba(205, 122, 55, 0.5);
          }

          .checked-base {
            border: 1px solid @button_outline_color;
            background: url('../../assets/img/pool-mobile.svg');
            background-size: 100%;
          }

          .checked-base:hover {
            border: 1px solid @button_outline_color;
          }

        }

        .slide-icon {
          display: none;
        }

        .some-coin {
          width: 342px;
          max-height: 280px;
          overflow: scroll;
          overflow-x: hidden;
          margin: 0 auto;
          margin-left: 24px;
          margin-top: 16px;

          .coin-box {
            width: 319px;
          }

          .coin-box {
            height: 70px;
          }
        }

        .some-coin::-webkit-scrollbar {
          border-radius: 200px;
          width: 8px;
          background-color: rgba(255, 255, 255, 0.1);
        }

        .some-coin::-webkit-scrollbar-track {
          box-shadow: inset 0 0 0px rgb(20, 25, 36);
          border-radius: 10px;
          background-color: rgba(255, 255, 255, 0.1);
        }

        .some-coin::-webkit-scrollbar-thumb {
          border-radius: 10px;
          box-shadow: inset 0 0 0px rgb(20, 25, 36);
          background-color: #203B60;
        }
      }

      .pool-info-text {
        padding-left: 24px;

        .pool-network-type {
          display: flex;
          align-items: center;
          padding-top: 42px;

          .pool-type-network-text {
            margin-left: 12px;
            font-weight: 600;

            .pool-type-text {
              color: #FFFFFF;
            }

            .pool-network-text {
              font-size: 14px;
            }
          }

          .network-logo {
            img {
              width: 28px;
              height: 28px;
            }
          }
        }

        .pool-des {
          padding-top: 47px;
          padding-right: 74px;
          font-size: 14px;

          a {
            text-decoration: none;
          }

          .learn-more {
            color: @button_outline_color;
          }
        }
      }
    }

    .pool-detail-add-remove-info {
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .mining-deri-liquidity {
        .btoken-liquidity {
          display: flex;
          align-items: center;
          padding-left: 24px;
          padding-top: 40px;

          .manage {
            font-size: 24px;
            color: @whiteFontColor;
            margin-left: 16px;
            font-weight: 600;
            height: 38px;
            line-height: 38px;
          }


          .btoken-logo {
            width: 40px;
            height: 40px;
            background-color: #203B60;
            border-radius: 50%;
          }

          .lp-btoken-logo {
            display: flex;
            align-items: center;

            .btoken-logo {
              margin-right: 0;
            }

            .conect-logo {
              font-size: 20px;
              margin: 0 5px;
            }
          }


        }

        .deri-current-epoch {
          min-height: 105px;

          .mining-pool-claim {
            border-bottom: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 100px;
            padding: 0 24px;
            margin-top: 20px;

            .mining-claim-info {
              font-weight: 500;

              .mining-pool-num {
                display: inline-block;
                min-width: 126px;
                height: 32px;
                text-align: center;
                border: 1px solid #93A1B8;
                border-radius: 8px;
                cursor: pointer;
                font-size: 20px;
                color: #E0ECFF;
                margin: 0 8px;

                img {
                  margin-left: 8px;
                  position: relative;
                  top: 3px;
                }

                .spinner-border {
                  position: relative;
                  top: -3px;
                }
              }

              .no-deri {
                opacity: 0.5;
              }
            }
          }

          .epoch-pool {
            height: 80px;
            margin-top: 0;
          }
        }
      }

      .mining-add-remove-liquidity {
        .add-remove-liquidity {
          font-weight: 600;
        }

        .pool-info {
          .pool-info-title {
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: @whiteFontColor;
            font-size: 18px;

            .down-up {
              display: block;
              cursor: pointer;

              .up-icon {
                transform: rotate(180deg);
              }
            }
          }

          .show {
            display: block;
          }

          .hide {
            display: none;
          }

          .pool-info-text-one {
            padding-top: 16px;
            padding-bottom: 24px;
          }

          .pool-info-text-one,
          .pool-info-text-two {
            .info-box {
              display: flex;
              height: 40px;
              justify-content: space-between;
              font-size: 14px;
              align-items: center;

              .info-box-num {
                color: @whiteFontColor;

                div {
                  display: inline-block;
                }

                .collect {
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  width: 65px;
                  height: 16px;
                  background: rgba(55, 86, 205, 0.3);
                  border-radius: 100px;
                  font-size: 10px;
                  font-weight: 500;
                  margin-left: 5px;
                }

                a {
                  color: @whiteFontColor;
                  text-decoration: none;
                }
              }

              .collect-box {
                display: flex;
              }

              .symbols {
                max-width: 204px;

                .text {
                  max-width: 204px;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                }
              }
            }
          }
        }
      }
    }
  }
}


@media only screen and (max-width:759px) {
  .pool-detail {
    min-width: 375px;

    .deri-num-slider {
      margin-top: 24px;
    }

    .deri-claim-slider {
      .time-slider {
        width: 327px;
      }
    }

    .back-pool {
      display: flex;
      align-items: center;
      padding-top: 16px;
      padding-left: 24px;
      height: 48px;
    }

    .pool-deatail-info {
      .pool-detail-basetoken-info {
        .pool-info-text {
          border-bottom: @border;

          .pool-network-type {
            padding-top: 24px;
          }

          .pool-des {
            padding-top: 16px;
            margin-bottom: 16px;
          }
        }

        .select-basetoken-info {
          padding: 0 24px;

          .select-coin-title-search {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }

          .select-coin-title {
            margin-top: 24px;
            width: 221px;
            height: 49px;
            padding-left: 0;
          }

          .coin-search {
            display: flex;
          }

          .coin-list {
            min-width: 327px;
            display: flex;
            border: @border;
            border-radius: 4px;
            margin: 24px auto;

            .checked-base {
              background-size: auto;
            }

            .coin-box {
              min-width: 109px;
              height: 92px;
              flex: 1;
              display: block;
              padding: 0 7px;
              padding-top: 14px;

              font-size: 11px;

              .base-apy-info {
                width: 90px;
                margin: 0 auto;
                height: 31px;
              }

              .btoken-logo {
                width: 24px;
                height: 24px;
                background-size: 13px 13px;
                margin-right: 10px;
              }

              .base-name {
                font-size: 11px !important;
              }

              .stake {
                margin: 0 auto;
                margin-top: 10px;

              }
            }

            .coin-box:hover {
              border-radius: 4px;
            }
          }

          .slide-icon {
            display: block;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
          }

          .left {
            left: -20px;

          }

          .right {
            right: -20px;
          }

          .some-coin {
            width: 100%;
            overflow-y: unset;
            overflow-x: auto;

            .coin-box {
              min-width: 191px;

              .base-apy-info {
                align-items: center;
                width: 176px;
              }
            }
          }

          .some-coin::-webkit-scrollbar {
            display: none;
          }
        }
      }

      .pool-detail-add-remove-info {
        border-top: @border;

        .mining-deri-liquidity {
          border-bottom: @border;

          .btoken-liquidity {
            padding-top: 18px;
          }

          .deri-current-epoch {
            margin-top: -24px;
            min-height: 105px;

            .mining-pool-claim {
              height: 86px;
            }
          }
        }

        .mining-add-remove-liquidity {
          .pool-info {
            min-height: 78px;
            border-bottom: @border;
            padding: 0 24px;
            padding-top: 24px;

            .pool-info-title {
              height: 30px;
              line-height: 30px;
            }
          }

          .tab-header {
            width: 100%;
          }
        }
      }
    }
  }
}

@media only screen and (max-width:610px) {
  .pool-detail {
    .pool-deatail-info {
      .pool-detail-basetoken-info {
        .select-basetoken-info {
          .select-coin-title-search {
            display: block;
          }
        }
      }
    }
  }
}

@media only screen and (min-width : 760px) and (max-width:1140px) {
  .pool-detail {
    .back-pool {
      padding-left: 48px;
      padding-top: 24px;
    }

    .deri-num-slider {
      .deri-cliam-remaining {
        margin-top: 0;
      }

      .deri-claim-slider {
        margin: 4px 0;

        .time-slider {
          width: 100%;
        }
      }
    }

    .pool-deatail-info {
      width: 760px;
      border: @border;
      margin: 0 auto;
      display: flex;

      .pool-detail-basetoken-info {
        width: 380px;

        .pool-info-text {
          height: 234px;
          border-bottom: @border;
        }

        .select-basetoken-info {

          .select-coin-title {
            width: 221px;
            padding-top: 32px;
            color: @whiteFontColor;
          }

          .some-coin {
            max-height: 700px;
          }
        }
      }

      .pool-detail-add-remove-info {
        width: 380px;
        border-left: @border;

        .mining-deri-liquidity {
          height: 234px;
          border-bottom: @border;

        }

        .mining-add-remove-liquidity {
          .pool-info {
            width: 380px;
            padding: 0 24px;
            border-right: @border;
            border-bottom: @border;
            min-height: 97px;

            .pool-info-title {
              height: 32px;
              padding-top: 32px;
            }
          }
        }
      }
    }
  }

}

@media only screen and (min-width : 1140px) {
  .pool-detail {
    .back-pool {
      padding-left: 48px;
      padding-top: 24px;
    }

    .pool-deatail-info {
      width: 1140px;
      height: 691px;
      border: @border;
      margin: 0 auto;
      display: flex;

      .pool-detail-basetoken-info {
        width: 380px;

        .pool-info-text {
          height: 234px;
          border-bottom: @border;
        }

        .select-basetoken-info {
          height: 457px;

          .select-coin-title {
            width: 221px;
            padding-top: 32px;
            color: @whiteFontColor;
          }
        }
      }

      .pool-detail-add-remove-info {
        width: 760px;
        height: 691px;
        border-left: @border;

        .mining-deri-liquidity {
          height: 234px;
          border-bottom: @border;

          .manage-stake {
            display: flex;
            align-items: center;
          }
        }

        .mining-add-remove-liquidity {
          display: flex;
          height: 457px;

          .pool-info {
            width: 380px;
            padding: 0 24px;
            border-right: @border;

            .pool-info-title {
              height: 32px;
              margin-top: 17px;

              .down-up {
                display: none;
              }
            }
          }
        }
      }
    }
  }

  html[lang=zh-TW],
  html[lang=ko],
  html[lang=ja] {
    .pool-detail .pool-deatail-info .pool-detail-basetoken-info .select-basetoken-info .select-coin-title {
      width: 240px;
    }
  }

}

html[lang=id_ID]{
  .pool-detail .pool-deatail-info .pool-detail-basetoken-info .select-basetoken-info .select-coin-title {
    width: 270px;
  }
}
html[lang="ru"] {
  .pool-detail .pool-deatail-info .pool-detail-basetoken-info .select-basetoken-info .select-coin-title {
    width: 353px;
  }
}

html[lang=ko],
html[lang=ja],
html[lang=tr] {
  .pool-detail .pool-deatail-info .stake {
    width: 100px;
  }
}

html[lang=id_ID] {
  .pool-detail .pool-deatail-info .stake {
    width: 120px;
  }
}

html[lang=es] {
  .pool-detail .pool-deatail-info .stake {
    width: 150px;
  }
}