.operate-liquidity {
  height: 410px;
  font-weight: 600;
  position: relative;

  .remove-hint {
    padding-left: 24px;
    padding-top: 10px;
    position: absolute;
    top: 112px;
  }

  .add-input {
    display: flex;
    justify-content: center;
    padding-top: 35px;
  }

  .wallet-balance {
    padding-left: 24px;
    padding-top: 8px;
  }

  .check-proportion {
    width: 332px;
    height: 40px;
    margin: 0 auto;
    margin-top: 32px;
  }

  .information {
    width: 332px;
    height: 72px;
    margin: 0 auto;
    margin-top: 32px;

    .percentage,
    .staked-balance {
      display: flex;
      height: 40px;
      justify-content: space-between;
      font-size: 14px;
      align-items: center;

      .info-box-num {
        color: #E0ECFF;
      }
    }

    .isBTokenAdequate {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 16px;
      border: 1px solid rgba(206, 94, 94, 0.2);
      font-size: 12px;
      height: 64px;

      img {
        margin-right: 10px;
      }
    }
  }

  .add-remove-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 19px;

    .magrined {
      font-size: 14px;
      color: #E0ECFF;
      border-bottom: 1px solid #FFF;
      margin-bottom: 4px;
      cursor: pointer;
    }
  }
}

.operate-liquidity-lp {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .no-nft-box {
    display: flex;
    width: 243px;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    div {
      text-align: center;
      font-weight: 500;
      font-size: 16px;
      color: #E0ECFF;
    }
  }

  .add-remove-nft-box {
    width: 332px;
    height: 332px;
    color: #E0ECFF;
    font-size: 16px;

    .tokenIdList {
      height: 80%;
      overflow: auto;
      overflow-x: hidden;

      .tokenId-box {
        width: 332px;
        height: 48px;
        margin-top: 18px;
        display: flex;
        align-items: center;
        padding-left: 14px;
        background: rgba(85, 119, 253, 0.1);
        border-radius: 4px;

        .color-box {
          width: 17px;
          height: 17px;
          border-radius: 50%;
          background-color: #824A4A;
        }

        .tokenId-text {
          margin-left: 12px;
        }
      }
    }
  }

  .add-remove-btn {
    position: absolute;
    bottom: 33px;
  }
}